import React, { Fragment, useEffect, useState } from "react";
import { Link } from "gatsby";
import { Button, Card, Col, Container, Row } from "react-bootstrap";
import moment from "moment";
import Loading from "@components/common/loading";
import { utils } from "@core/helper";
import { collectionKeys } from "@core/helper/utils";

// import LoadMore from "@components/common/loadmore";
// import { collection, doc, getDocs, setDoc } from "firebase/firestore/lite";
// import ImageFallback from "@components/common/imagefallback";
// import { collection, doc, getDocs, query, setDoc } from "firebase/firestore/lite";

const BlogsData = (props) => {
  const { db } = props;
  let count = 0;
  
  // const { numberItemsPagination, loadMoreItems, isLoading } = LoadMore(6);
  const [trendingBlogs, setTrendingBlogs] = useState([]);
  const [isMounted, setIsMounted] = useState(false);
  const [blogsList, setBlogsList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [initLoading, setInitLoading] = useState(true);
  const [loadMoreLoading, setLoadMoreLoading] = useState(false);

  const [lastVisible, setLastVisible] = useState(null);
  const [hasMore, setHasMore] = useState(true);

  const loadBlogsList = async () => {
    try {
      if (!hasMore) return;
      setLoadMoreLoading(true);

      // const oldCollectionRef = collection(db, collectionKeys.BLOG); // Source collection
      // const newCollectionRef = collection(db, collectionKeys.BLOG); // Destination collection

      // const querySnapshot = await getDocs(oldCollectionRef);
      // for (let docSnap of querySnapshot.docs) {
      //   const data = docSnap.data();
      //   const docId = docSnap.id; // Keep the same document ID
      //   const newData = {
      //     ...data,
      //     path: "static", // Set new key-value pair
      //   };
      //   await setDoc(doc(newCollectionRef, docId), newData);
      // }

      const {
        data,
        lastVisible: newLastVisible,
        hasMore: moreData,
      } = await utils.fetchPaginatedData(
        db,
        collectionKeys.BLOG,
        "lastUpdate",
        6,
        lastVisible
      );
      setBlogsList((prevData) => [...prevData, ...data]);
      setLastVisible(newLastVisible);
      setHasMore(moreData);
    } catch (error) {
      console.log("er::", error);
    } finally {
      setInitLoading(false);
      setLoading(false);
      setLoadMoreLoading(false);
    }
  };

  useEffect(() => {
    if (isMounted && db) {
      
      loadBlogsList(db);
    }
  }, [isMounted, db]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  useEffect(() => {
    if (isMounted) {
      if (blogsList?.length > 0) {
        const sortTrendingBlogs = async (db, blogs) => {
          let addViewCountArr = [];
          let blogsCountsArr = await utils.getDocData(
            db,
            collectionKeys.VIEWCOUNT
          );
          let blogsListArr = await utils.getDocData(db, collectionKeys.BLOG);
          for (let blogObj of blogsListArr) {
            for (let blogsCountDoc of blogsCountsArr) {
              if (blogObj?.slug === blogsCountDoc?.key) {
                blogObj.viewCount = blogsCountDoc?.count;
                addViewCountArr.push(blogObj);
              }
            }
            setTrendingBlogs(addViewCountArr);
          }
        };
        sortTrendingBlogs(db, blogsList);
      }
    }
  }, [isMounted, db, blogsList]);

  trendingBlogs.sort((a, b) => b.viewCount - a.viewCount);

  return (
    <Container fluid className="p-0">
      <Fragment>
        <div className="blog_details_bg">
          <h1>Trending Blogs</h1>
          {loading && (
            <div className="loader_wrapper">
              <Loading variant={"light"} />
            </div>
          )}
          {!loading && blogsList?.length === 0 && (
            <div className="loader_wrapper">
              <h3>No Blogs Found</h3>
            </div>
          )}
        </div>
        {/* {!loading && ( */}
        <div className="blog_list_bg">
          <Container className="blog_container_position">
            <Row className="blog_row_gap">
              {trendingBlogs?.slice(0, 6).map((ele, ind) => {
                let blogId = ind;
                if (blogId % 2 === 0) {
                  count = count + 1;
                }
                return (
                  <Col key={ind} lg={6} xs={12} className="p-lg-0">
                    {count % 2 !== 0 ? (
                      <Card key={ele.id} className="jt_blog_card">
                        <Row className="mx-lg-0">
                          <Col sm={6} xs={12} className="p-sm-0">
                            <Link
                              to={
                                ele?.path === "static"
                                  ? `${ele?.slug}`
                                  : `/${ele?.slug}`
                              }
                              className="text-decoration-none  d-block "
                            >
                              <div
                                id="arrow_right"
                                className="jt_blog_img_wrapper"
                              >
                                <img
                                  src={`/images/blogImages/${ele?.slug}.webp`}
                                  alt={ele?.image_alt}
                                  className="jt_blog_img"
                                />
                                <div className="blog_arrow_first">
                                  <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    width="15"
                                    height="18"
                                    viewBox="0 0 15 18"
                                    fill="none"
                                  >
                                    <path
                                      d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                      fill="white"
                                    />
                                  </svg>
                                </div>
                              </div>
                            </Link>
                          </Col>
                          <Col
                            sm={6}
                            xs={12}
                            className="p-sm-0 d-flex justify-content-center "
                          >
                            <Link
                              to={
                                ele?.path === "static"
                                  ? `${ele?.slug}`
                                  : `/${ele?.slug}`
                              }
                              className="jt_blog_card_body"
                            >
                              <p className="jt_blog_date">
                                {moment(ele?.createdAt).format("DD MMMM YYYY")}
                              </p>

                              <p className="jt_blog_title">{ele?.title}</p>
                              <p className="jt_blog_description">
                                {ele?.short_description.slice(0, 65) + "..."}
                              </p>
                            </Link>
                          </Col>
                        </Row>
                      </Card>
                    ) : (
                      <Card key={ele.id} className="jt_blog_card">
                        <Row className="mx-lg-0">
                          <Col sm={6} xs={12} className="p-sm-0 order_two">
                            <Link
                              to={
                                ele?.path === "static"
                                  ? `${ele?.slug}`
                                  : `/${ele?.slug}`
                              }
                              className="jt_blog_card_body"
                            >
                              <p className="jt_blog_date">
                                {moment(ele?.createdAt).format("DD MMMM YYYY")}
                              </p>

                              <p className="jt_blog_title">{ele?.title}</p>
                              <p className="jt_blog_description">
                                {ele?.short_description.slice(0, 65) + "..."}
                              </p>
                            </Link>
                          </Col>
                          <Col sm={6} xs={12} className="p-0 order_one">
                            <Link
                              to={
                                ele?.path === "static"
                                  ? `${ele?.slug}`
                                  : `/${ele?.slug}`
                              }
                              className="text-decoration-none  d-block "
                            >
                              <div
                                id="arrow_right"
                                className="jt_blog_img_wrapper"
                              >
                                <img
                                  src={`/images/blogImages/${ele?.slug}.webp`}
                                  alt={ele?.image_alt}
                                  className="jt_blog_img"
                                />

                                <div className="blog_arrow_second">
                                  <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    width="15"
                                    height="18"
                                    viewBox="0 0 15 18"
                                    fill="none"
                                  >
                                    <path
                                      d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                      fill="white"
                                    />
                                  </svg>
                                </div>
                              </div>
                            </Link>
                          </Col>
                        </Row>
                      </Card>
                    )}
                  </Col>
                );
              })}
            </Row>
          </Container>
        </div>
        {/* )} */}
      </Fragment>
      <div
        className="blog_details_bg"
        style={{
          background: "transparent",
        }}
      >
        <h2
          style={{
            color: "#7534fc",
          }}
        >
          Latest Blogs
        </h2>
        {/* {loading && (
          <div className="loader_wrapper">
            <Loading variant={"dark"} />
          </div>
        )} */}
      </div>

      <div
        className="blog_list_bg"
        style={{
          background: "transparent",
        }}
      >
        <Container className="blog_container_position">
          <Row className="blog_row_gap">
            {blogsList?.map((ele, ind) => {
              let blogId = ind;
              if (blogId % 2 === 0) {
                count = count + 1;
              }
              return (
                <Col key={ind} lg={6} xs={12} className="p-lg-0">
                  {count % 2 !== 0 ? (
                    <Card key={ele.id} className="jt_blog_card">
                      <Row className="mx-lg-0">
                        <Col sm={6} xs={12} className="p-sm-0">
                          <Link
                            to={
                              ele?.path === "static"
                                ? `${ele?.slug}`
                                : `/${ele?.slug}`
                            }
                            className="text-decoration-none d-block "
                          >
                            <div
                              id="arrow_right"
                              className="jt_blog_img_wrapper"
                            >
                              {/* <img
                                src={`/images/blogImages/${ele?.slug}.webp`}
                                alt={ele?.image_alt}
                                className="jt_blog_img"
                              /> */}
                              <img
                                src={`/images/blogImages/${ele?.slug}.webp`}
                                alt={ele?.image_alt}
                                className="jt_blog_img"
                              />
                              <div className="blog_arrow_first">
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="15"
                                  height="18"
                                  viewBox="0 0 15 18"
                                  fill="none"
                                >
                                  <path
                                    d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                    fill="white"
                                  />
                                </svg>
                              </div>
                            </div>
                          </Link>
                        </Col>
                        <Col sm={6} xs={12} className="p-sm-0">
                          <Link
                            to={
                              ele?.path === "static"
                                ? `${ele?.slug}`
                                : `/${ele?.slug}`
                            }
                            className="jt_blog_card_body"
                          >
                            <p className="jt_blog_date">
                              {moment(ele?.createdAt).format("DD MMMM YYYY")}
                            </p>
                            <p className="jt_blog_title">{ele?.title}</p>
                            <p className="jt_blog_description">
                              {ele?.short_description.slice(0, 65) + "..."}
                            </p>
                          </Link>
                        </Col>
                      </Row>
                    </Card>
                  ) : (
                    <Card key={ele.id} className="jt_blog_card">
                      <Row className="mx-lg-0">
                        <Col sm={6} xs={12} className="p-sm-0 order_two">
                          <Link
                            to={
                              ele?.path === "static"
                                ? `${ele?.slug}`
                                : `/${ele?.slug}`
                            }
                            className="jt_blog_card_body"
                          >
                            <p className="jt_blog_date">
                              {moment(ele?.createdAt).format("DD MMMM YYYY")}
                            </p>

                            <p className="jt_blog_title">{ele?.title}</p>
                            <p className="jt_blog_description">
                              {ele?.short_description.slice(0, 65) + "..."}
                            </p>
                          </Link>
                        </Col>
                        <Col sm={6} xs={12} className="p-0 order_one">
                          <Link
                            to={
                              ele?.path === "static"
                                ? `${ele?.slug}`
                                : `/${ele?.slug}`
                            }
                            className="text-decoration-none  d-block "
                          >
                            <div
                              id="arrow_right"
                              className="jt_blog_img_wrapper"
                            >
                              <img
                                src={`/images/blogs/${ele?.slug}.png`}
                                alt={ele?.image_alt}
                                className="jt_blog_img"
                              />

                              <div className="blog_arrow_second">
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="15"
                                  height="18"
                                  viewBox="0 0 15 18"
                                  fill="none"
                                >
                                  <path
                                    d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                    fill="white"
                                  />
                                </svg>
                              </div>
                            </div>
                          </Link>
                        </Col>
                      </Row>
                    </Card>
                  )}
                </Col>
              );
            })}

            {!initLoading && hasMore && (
              <Col md={12}>
                <Button
                  disabled={loadMoreLoading}
                  className="blog_load_btn"
                  onClick={loadBlogsList}
                >
                  Load more {loadMoreLoading && <Loading />}
                </Button>
              </Col>
            )}
          </Row>
        </Container>
      </div>

      {/* {loading && <p>Loading...</p>}
      {hasMore && !loading && (
        <button onClick={loadData}>Load More</button>
      )} */}
    </Container>
  );
};

export default BlogsData;
